import React from 'react';
import { Cell, List, Panel, PanelHeader, Group } from '@vkontakte/vkui';

//иконки
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; // Коронавирус
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline'; // О приложении
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline'; // Настройки
import Icon28MessageAddBadgeOutline from '@vkontakte/icons/dist/28/message_add_badge_outline'; // Обратная связь

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader>Прайд</PanelHeader>
        <Group>
      <List>
        <Cell expandable before={<Icon28BugOutline />} onClick={props.go} data-to="covid">Коронавирус</Cell>
        <Cell expandable before={<Icon28ArticleOutline />} onClick={props.go} data-to="info">О приложении</Cell>
        <Cell expandable before={<Icon28SettingsOutline />}>Настройки (в разработке)</Cell>
        <Cell expandable before={<Icon28MessageAddBadgeOutline />} Link href="/krev1083829298383930202928848383">Обратная связь</Cell>
      </List>
    </Group>

    </Panel>
);

export default Home;